import React, {useState, useContext } from "react"
import ThemeContext from "../utils/theme"
import { Navbar, Nav, Form, Image, Button, Modal, CloseButton } from "react-bootstrap"
import { Link } from "gatsby"
import "./Fontawesome.js"
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"

import { loggedUser } from  "./FirebaseAuth"

export default () => {
  const { dark, toggleDark, toString } = useContext(ThemeContext)
  let currentUser = loggedUser()

  const [HowModal, setHowModal] = useState(false);

  function How2PlayModal(props) 
  {
    return (
      
      <Modal
        {...props}
        size="lg"
        aria-labelledby="contained-modal-title-vcenter"
        centered
        className = "my-modal"
      >
        <Modal.Header style = {{backgroundColor: `${dark ? '#222' : 'white'}`,color: `${dark ? 'white' : 'black'}`}}>
          <Modal.Title  id="contained-modal-title-vcenter">
            How to Play
          </Modal.Title>
          <CloseButton onClick={props.onHide} variant = {`${dark ? 'white' : ''}`} />
        </Modal.Header>

        <Modal.Body style = {{backgroundColor: `${dark ? '#222' : 'white'}`,color: `${dark ? 'white' : 'black'}`}}>
          <h5 style={{textAlign:"left"}}> 
            <ul>
              <li>
                Go to the <Link to="/">home page</Link> and click on any of the homes. 
              </li>
              <br/>
              <li>
                Look through the photos and details to get an idea of everything the home has to offer.
              </li>
              <br/>
              <li>
                Guess the price of the home by inputting a number into the top text bar!
              </li>
              <br/>
              <li>
                <span style={{color:"#00DD00"}}>Create an account to keep track of your score.</span>
              </li>
            </ul>
          </h5>
        
        </Modal.Body>

        <Modal.Footer style = {{backgroundColor: `${dark ? '#222' : 'white'}`,color: `${dark ? 'white' : 'black'}`}}>
          <Button variant="danger" onClick={props.onHide}>Close</Button>
        </Modal.Footer>
      </Modal>
    );
  }
  return (
    <>
    <Navbar variant={toString()} fixed="top" collapseOnSelect expand="md" style={{paddingLeft:"1rem",paddingRight:"1rem"}}>
      <Navbar.Brand className="pl-5 " as={Link} to="/">
        {
        // <FontAwesomeIcon
        //   icon={["fab", `${dark ? "empire" : "rebel"}`]}
        //   className={`brand-icon ${dark ? "empire" : "rebel"}`}
        //   title="Home"
        // />
        }
        <Image
          width="150"
          fluid
          src={dark ? `../../icons/Appraisir-Logo-White.png` : `../../icons/Appraisir-Logo.png`}
          alt={"Appraisir"}
        />
      </Navbar.Brand>
      <Image
          width="30"
          fluid
          src={`../../icons/Question.png` }
          alt={"How"}
          onClick={()=>{setHowModal(true)}}
          style={{cursor:'pointer'}}
        />
      <Navbar.Toggle aria-controls="responsive-navbar-nav" />
      <Navbar.Collapse
        id="responsive-navbar-nav"
        className="justify-content-end"
      >
        <Nav className="pr-3 mr-4 nav-links">
          {
          // <Nav.Link className="ml-2" as={Link} to="/blog" title="Blog">
          //   Blog
          // </Nav.Link>
          // <Nav.Link className="ml-2" as={Link} to="/about" title="About">
          //   About
          // </Nav.Link>
          // <Nav.Link className="ml-2" as={Link} to="/projects" title="Projects">
          //   Projects
          // </Nav.Link>
          // <Nav.Link className="ml-2" as={Link} to="/resume" title="Resume">
          //   Resume
          // </Nav.Link>
          }
          <Nav.Link className="ml-2" as={Link} to="/" title="Home">
            Home
          </Nav.Link>
          <Nav.Link className="ml-2" as={Link} to="/About" title="About">
            About
          </Nav.Link>
          <Nav.Link className="ml-2" as={Link} to={currentUser ?  "/Dashboard" : "/Login"} title={currentUser ?  "Dashboard" : "Login"}>
            {currentUser ?  "Profile" : "Log In"}
          </Nav.Link>
          <Form className="ml-3 my-auto">
            <Form.Check
              type="switch"
              id="custom-switch"
              label=""
              title="Toggle Theme"
              checked={dark}
              onChange={toggleDark}
            />
          </Form>
        </Nav>
      </Navbar.Collapse>
    </Navbar>
    {HowModal ?  <How2PlayModal
                    show={HowModal}
                    onHide={() => setHowModal(false)}
                  /> : ` `}
    
    </>
  )
}
